*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main{
  height:100%;
  width:100%;
}

video{
  width: 100%;
  height:100vh;
   object-fit: cover;
   position: absolute;
}

.intro{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;  
  width:100%;
  height:100vh;

}

.intro h1,h3{
  color:#fff;
  text-shadow: rgb(28, 22, 22) 1px 3px;
  padding:1rem;
  letter-spacing: .1rem;
  text-align: center;
}
h1{
  font-size: 3rem;
}
 h3{
  font-size: 1.5rem;
 }


 .overlay{
  width:100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(50, 85, 85, .2);
 }






 button{
   position: relative;
   top: 650px;
   border: none;
   background-color: transparent;
   margin:.5rem;
   /*background-color: #fff;
   border-radius: 50%;*/
   padding:1rem;
   font-size: 2rem;
   color:aqua;
    
 }